<template>
  <div class="link-create">
    <section class="section">
      <div class="container">
        <div class="columns is-multiline">
          <div class="column is-8">
            <b-field
              label="Name *">
              <b-input
                v-model="form.name"
                ></b-input>

            </b-field>
            <p v-if="error">
              {{ error.name }}
            </p>
            <hr>
            <b-field
              label="Url *">
              <b-input
                v-model="form.url"
                :placeholder="placeholder"></b-input>
            </b-field>
            <p v-if="error">
              {{ error.url }}
            </p>
          </div>
          <div class="column is-4">
            <b-button
              type="is-primary"
              label="Create"
              @click.prevent="newLink()"></b-button>
            <!-- <div class="card">
              <div class="card-header is-shadowless">
                <p class="card-header-title has-text-primary is-size-6">
                  Select Type Link *
                </p>
              </div>
              <div class="card-body">
                <div class="card-content">
                  <div class="field">
                      <b-radio
                        v-for="item in accept.data"
                        :key="item.rule"
                        v-model="radio"
                        :native-value="item.id">
                          {{ item.name }}
                      </b-radio>
                  </div>
                </div>
              </div>
            </div> -->
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Link from '../../api/Link'
// import UrlAccept from '../../api/UrlAccept'
import _ from 'lodash'

export default {
  name: 'LinkCreate',
  data () {
    return {
      error: [],
      form: {
        name: '',
        url: '',
        accept: ''
      },
      accept: [],
      radio: '',
      placeholder: 'Wait select type!'
    }
  },
  methods: {
    newLink: function (){
      Link.createLink(this.form).then((response) => {
        if(response.status === 200) {
          this.form = {
            name: '',
            url: '',
            accept: ''
          }
          this.$buefy.notification.open({
              message: 'Create successfull!',
              type: 'is-success'
          })
        }
      }).catch((error) => {
        if(error.response.status === 422) {
          this.error = error.response.data.errors
        }
      })
    }
  },
  mounted () {
    // UrlAccept.getAccept().then((response) => {
    //   this.accept = response.data
    // }).catch((error) => {
    //   console.log(error)
    // })
  },
  watch: {
    radio: function (val) {
      this.form.accept = val
      this.placeholder = `Example url ${_.find(this.accept.data, { id: val }).rule}`
    }
  }
}
</script>
